.home {
    .swiper-container {
        background: #fff; // height: auto;
        // padding-left: 50%;
        img {
            width: 100%;
            max-height: 100%;
        }
        .swiper-pagination-bullet {
            background: #fff;
            opacity: .8;
            transition: width ease 0.3s;
        }
        .swiper-pagination-bullet-active {
            background: #fff;
            opacity: 1;
            width: 12px;
            height: 8px;
            border-radius: 0.5rem;
            transition: width ease 0.3s;
        }
        .swiper-pagination {
            text-align: right;
            width: 98%;
        }
    }
}

.swiper-container {
    background: #fff; // height: auto;
    // padding-left: 50%;
    img {
        width: 100%;
        max-height: 100%;
    }
    .swiper-pagination-fraction{
        left: inherit;
        right: 10px;
        display: inline-block;
        width: auto;
        padding: 0 8px;
        background: rgba(0,0,0,.2);
        border-radius: 40px;
        color: #fff;
        font-size: 12px;
        

    }
}


/*导航*/

.nav-container {
    background: #fff;
    padding: 8px 0 5px 0;
    ul {
        display: flex;
        flex-wrap: wrap;
        padding-bottom: .15rem;
        li {
            flex-basis: 25%;
            text-align: center;
            align-items: center;
            a {
                padding: .26rem 0;
                display: block;
                color: #333;
                text-decoration: none;
                font-size: 12px;
                &:active {
                    background: #efefef;
                }
            }
        }
    }
    .nav-icon {
        display: block;
        overflow: hidden;
        width: 2.5rem;
        height: 2.8rem;
        margin: auto;
        img{
            width: 100%;;
            height: 94%;
        }
    }
}


