.btn-wrap {
  min-height: 50px;
}
.btn-fixed {
  position: fixed;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 4;
  box-shadow: 0 -5px 5px rgba(136, 136, 136, 0.48);
}
.ui-btn {
  -webkit-appearance: none;
  border: none;
  display: inline-block;
  padding: 10px 20px;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  background: #FFC107;
  text-align: center;
  border-radius: 2px;
  color: #fff;
  outline: none;
  box-sizing: border-box;
}
.ui-btn:active {
  opacity: .7;
}
.ui-btn[disabled],
.ui-btn.ui-btn-disabled {
  opacity: .5;
}
.ui-btn[disabled]:active,
.ui-btn.ui-btn-disabled:active {
  opacity: .5;
}
.ui-btn a {
  color: #fff;
}
.ui-btn-l2 {
  padding: 1em 0;
  font-size: 105%;
}
.ui-btn-block {
  display: block;
  width: 100%;
  text-align: center;
  border-radius: 0;
  border: none;
}
.btn-fixed .ui-btn-block {
  height: 55px;
  padding: 0;
  line-height: 55px;
}
.ui-btn-getCode {
  background: none;
  color: #FFC107;
  border: solid 1px #FFC107;
}
.ui-btn-setDef,
.ui-btn-edit {
  padding: 2px 5px;
  line-height: 1.5;
  font-size: 14px;
  background: none;
  border: solid 1px #aaa;
  color: #666;
}
.ui-btn-setDef i,
.ui-btn-edit i {
  font-size: inherit;
}
.ui-btn-small {
  padding: 4px 5px;
}
button,
input,
textarea,
select {
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
}
.ui-input {
  display: inline-block;
  width: 100%;
  box-sizing: border-box;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  border: solid 1px #ddd;
  padding: 4px 2px;
  outline: none;
  border-radius: 0;
  -webkit-appearance: none;
}
.ui-input[disabled] {
  opacity: .5;
}
.ui-checkbox {
  -webkit-appearance: none;
  background: none;
  border: none;
  padding: 0;
  width: 1.2em;
  font-size: 22px;
  color: #888;
  outline: none;
  margin: 0;
  display: inline-block;
  position: relative;
  height: 1.2em;
  vertical-align: middle;
}
.ui-checkbox:before {
  line-height: 1;
  font-family: "iconfont" !important;
  font-size: inherit;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\e692";
  display: inline-block;
  position: absolute;
  top: 2px;
  left: 0;
  height: 22px;
}
.ui-checkbox:checked::before {
  content: "\e640";
  color: #9973e4;
}
.uiSelect {
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.ui-select {
  position: relative;
  flex: 1;
}
.ui-select ~ .ui-select {
  margin-left: 10px;
}
.ui-select select {
  -webkit-appearance: none;
  border: solid 1px #aaa;
  width: 100%;
  display: block;
  padding: 5px;
  box-sizing: border-box;
  color: #666;
  font-family: inherit;
  font-size: 14px;
  margin: 0;
  background: none;
  border-radius: 2px;
  text-align: center;
  outline: none;
}
.ui-select select option {
  text-align: center;
  display: block;
}
.ui-select:after {
  content: '';
  position: absolute;
  bottom: 2px;
  right: 2px;
  border-right: solid 5px #aaa;
  border-bottom: solid 5px #aaa;
  border-top: solid 5px transparent;
  border-left: solid 5px transparent;
}
