/*回到顶部*/
#goto_top,
#goto_home {
  background-color: rgba(0, 0, 0, 0.5);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  position: fixed;
  display: block;
  z-index: 9999;
  bottom: 9.5rem;
  right: 5%;
  text-align: center;
  line-height: 44px;
}
#goto_top i,
#goto_home i {
  color: #fff;
  font-size: 22px;
  opacity: .9;
}
#goto_top a,
#goto_home a {
  text-decoration: none;
  color: inherit;
}
#goto_home {
  bottom: 6.5rem;
  display: block;
}
nav {
  width: 100%;
